import React, { useCallback, useEffect, useState } from 'react'
import { useEmblaCarousel } from 'embla-carousel/react'
import { PrevButton, NextButton } from './NavButton'
import { useInView } from 'react-intersection-observer'
import { SlideDefault } from './SlideDefault'
import { mediaByIndex } from '../Media'
import {
  SceneDefault,
  SceneDefaultViewport,
  SceneDefaultContainer,
  SceneDefaultPlaceholder,
} from './SceneDefault'
import { useInfiniteScroll } from '../useInfiniteScroll'
import { SlideLoading } from './SlideLoading'

const mockApiCall = (
  minWait: number,
  maxWait: number,
  callback: () => void,
): void => {
  const min = Math.ceil(minWait)
  const max = Math.floor(maxWait)
  const wait = Math.floor(Math.random() * (max - min + 1)) + min
  setTimeout(callback, wait)
}

const Carousel = () => {
  const [slides, setSlides] = useState([0, 1, 2, 3, 4])
  const [hasMoreToLoad, setHasMoreToLoad] = useState(true)
  const [prevBtnEnabled, setPrevBtnEnabled] = useState(false)
  const [nextBtnEnabled, setNextBtnEnabled] = useState(false)
  const [emblaRef, embla] = useEmblaCarousel({
    dragFree: true,
    inViewThreshold: 0,
    containScroll: 'trimSnaps',
    selectedClass: '',
    draggingClass: '',
    draggableClass: '',
  })
  const loadingMore = useInfiniteScroll(embla, slides, hasMoreToLoad)

  const scrollPrev = useCallback(() => embla && embla.scrollPrev(), [embla])
  const scrollNext = useCallback(() => embla && embla.scrollNext(), [embla])

  const onSelect = useCallback(() => {
    if (!embla) return
    setPrevBtnEnabled(embla.canScrollPrev())
    setNextBtnEnabled(embla.canScrollNext())
  }, [embla])

  useEffect(() => {
    if (!loadingMore) return
    mockApiCall(1000, 2000, () => {
      setSlides((currentSlides) => {
        if (currentSlides.length === 20) {
          setHasMoreToLoad(false)
          return currentSlides
        }
        const newSlideCount = currentSlides.length + 5
        return Array.from(Array(newSlideCount).keys())
      })
    })
  }, [setSlides, loadingMore])

  useEffect(() => {
    if (!embla) return
    embla.on('select', onSelect)
    embla.on('reInit', onSelect)
    onSelect()
  }, [embla, onSelect])

  return (
    <SceneDefault>
      <SceneDefaultViewport ref={emblaRef}>
        <SceneDefaultContainer>
          {slides.map((index) => (
            <SlideDefault
              key={index}
              width={100}
              imgSrc={mediaByIndex(index)}
            />
          ))}
          {hasMoreToLoad && <SlideLoading isLoading={loadingMore} />}
        </SceneDefaultContainer>
      </SceneDefaultViewport>
      <PrevButton onClick={scrollPrev} enabled={prevBtnEnabled} />
      <NextButton onClick={scrollNext} enabled={nextBtnEnabled} />
    </SceneDefault>
  )
}

const CarouselWithPlaceholder = () => {
  const [ref, inView] = useInView({ triggerOnce: true })

  return (
    <div ref={ref}>{inView ? <Carousel /> : <SceneDefaultPlaceholder />}</div>
  )
}

export default CarouselWithPlaceholder
