import React from 'react'
import styled from 'styled-components'
import { media } from '../../Settings'

export const SlideLoadingWrap = styled.div`
  position: relative;
  height: 100%;
  padding-left: 0.6rem;
  padding-top: 0.6rem;
  flex: 0 0 auto;
  min-width: 0;

  ${media.minSm} {
    padding-left: 0.8rem;
    padding-top: 0.8rem;
  }
  ${media.minMd} {
    padding-left: 1rem;
    padding-top: 1rem;
  }
`

export const SlideLoadingInner = styled.div`
  position: relative;
  height: 100%;
  overflow: hidden;
  padding: 2rem 6rem 2rem 4rem;
  width: 15rem;
  display: flex;
  align-items: center;
  justify-content: center;
`

export const SlideLoadingSpinner = styled.div`
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 4px solid rgba(27, 202, 205, 0.2);
  border-right: 4px solid rgba(27, 202, 205, 0.2);
  border-bottom: 4px solid rgba(27, 202, 205, 0.2);
  border-left: 4px solid #1bcacd;
  transform: translateZ(0);
  animation: loading 1.1s infinite linear;
  border-radius: 50%;
  width: 50px;
  height: 50px;

  &:after {
    border-radius: 50%;
    width: 50px;
    height: 50px;
  }

  @keyframes loading {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
`

type PropType = React.HTMLAttributes<HTMLDivElement> & {
  isLoading: boolean
}

export const SlideLoading = (props: PropType) => {
  const { isLoading, ...restProps } = props

  return (
    <SlideLoadingWrap {...restProps}>
      <SlideLoadingInner>
        {isLoading && <SlideLoadingSpinner />}
      </SlideLoadingInner>
    </SlideLoadingWrap>
  )
}
